// importer l'action qui a été faite
import { SET_STABLE_STATE } from "../actions/stable.action";

// le state initial est un objet vide
const initialState = {};

// il met à jour en temps réel le store
export default function filterReducer(state = initialState, action) {
  switch (action.type) {
    case SET_STABLE_STATE:
      return action.payload;
    default:
      return state;
  }
}
